import styled from 'styled-components';

export const ToolbarWrapper = styled.div`
  position: relative;
  background-color: #F7F8FA;
`;

export interface IDisabledWrapper {
  shouldDisable: boolean;
}

export const ToolButtonWrapper = styled.span<IDisabledWrapper>`
  opacity: ${ props =>  props.shouldDisable ? 0.35 : 1};
  pointer-events: ${ props => props.shouldDisable ? 'none' : 'initial'};
`; 
